import callbackStrem from "callback-stream";
import toStream from "to-stream";
import jsonTransform from "@rubeniskov/resume-transform-json";
import ymlTransform from "@rubeniskov/resume-transform-yml";
import jsonRenderer from "./renderers/json";
import ymlRenderer from "./renderers/yml"; // import svgRenderer from "./renderers/svg";

import xlsxRenderer from "./renderers/xlsx";
import mdRenderer from "./renderers/md";
var transforms = {
  json: jsonTransform,
  yml: ymlTransform
};
var renderers = {
  json: jsonRenderer,
  yml: ymlRenderer,
  // svg: svgRenderer,
  xlsx: xlsxRenderer,
  md: mdRenderer
};
/**
 * @typedef {"md" | "json" | "yml" | "xlsx"} ResumeOutputFormat
 */

/**
 * @typedef {"json" | "yml"} ResumeInputFormat
 */

/**
 *
 * @param {Buffer|string} data
 * @param {ResumeFormat} iformat
 * @param {ResumeFormat} oformat
 */

var resumeReader = function resumeReader(data, iformat, oformat, cb) {
  var transform = transforms[iformat];
  var renderer = renderers[oformat];
  console.log(iformat, oformat);

  if (!transform) {
    throw new TypeError("Unable to resolve input format \"".concat(iformat, "\""));
  }

  if (!renderer) {
    throw new TypeError("Unable to resolve output format \"".concat(oformat, "\""));
  }

  var source = toStream(data).pipe(transform()).pipe(renderer());

  if (typeof cb === "function") {
    source.pipe(callbackStrem(function (error, data) {
      return cb(error, data[0]);
    }));
  }

  return source;
};

export default resumeReader;